import React, { Component } from 'react';
import ShowCardComponent from './ShowCard';
import LearnCardComponent from './LearnCard';

class CustomComponent extends Component {
    components = {
        ShowCard:ShowCardComponent,
        LearnCard: LearnCardComponent
    };
    render() {
       const TagName = this.components[this.props.tag || 'ShowCard'];
       return <TagName  card={this.props.currentCard}  
       dataLearnCard={this.props.dataLearnCard}
       cardLearn={this.props.currentCardLearn} 
       listCard={this.props.listCard}/>
    }
}
export default CustomComponent;


