import React,{Component} from 'react';
import './Lessondetail.css';

import {BrowserRouter as Router, Route, Link} from 'react-router-dom';


class Lessondetail extends Component{

	constructor(props)
	{
		super(props);
		let listcard = [];
		this.props.lesson.cards.map((card)=>listcard.push({...card,clicked:false}));
		
		this.state = { listcards : listcard};
		this.handleClickCard = this.handleClickCard.bind(this);
		this.fnrenderEdit = this.fnrenderEdit.bind(this);
		
	}
	handleClickCard(card)
	{

		return (event)=>
		{
			
			const clicked = card.clicked;
			
			const {listcards} = this.state;
			const index = listcards.indexOf(card);
			//console.log(index);
			

			this.setState({
					listcards: [
					...listcards.slice(0,index),
					{
						...card, 
						clicked:!clicked 
					},
					...listcards.slice(index+1)
				]

			});
			

		}
	}

	fnrenderEdit(path)
	{
		if(this.props.edit ===true)
		return(
			<Link to={path+"/edit"}>
				<button className="btn-detail">
					Chỉnh Sửa Bộ Thẻ
				</button>
			</Link>
		);
		else return;
	}

	render()
	{
		let path = "/course/"+this.props.lesson._id;
		return(
			<div  className="container-lessondetail">
				<div  className="info">
					<div id="img-info">
            			<img src="http://data.minder.vn/courses/104000002.jpg"  width="200px" height="250px" alt=""/>
					</div>

					
			        <h3 id="title">
			            {this.props.lesson.title}
			        </h3>
					
					<p id="description">{this.props.lesson.description}</p>

					{

						this.fnrenderEdit(path)

					}

					<Link to={path+"/learn"}>
					<button className="btn-detail">
					Học Bộ Thẻ
					</button>
					</Link>
					<button className="btn-detail">
					Luyện Tập Bộ Thẻ
					</button>

					
				</div>
				<div  className="listWord">
				

				{
					this.state.listcards.map((card)=>
					{
						let classBack;
						let classFront; 
						if(card.clicked === true)
						{
							classBack = "back showBack";
							classFront = "front hideFront";
						}
						else
						{
							classBack = "back hideBack";
							classFront = "front showFront";
						}
						
			return(
				<div  className="word"  key={card._id} onClick={this.handleClickCard(card)}>
					<div  className={classFront} >
						<div className="concon">
					    	<h3>{card.front}</h3>
					    </div>
					</div>
	    			<div className={classBack}>
	    				<div className="concon">
		      				<h3>{card.back}</h3>
	      				</div>
	    			</div>
	    		</div>				
	    	)
			})
		}

				</div>

			</div>

		);
	}
}
export default Lessondetail;


