import React,{Component} from 'react';
import './KhoaHoc.css';
import Card from './Card';
import axios from 'axios';
import KhoaHoc from './KhoaHoc';
import Lessondetail from './Lessondetail';
import LessonEdit from './LessonEdit';
import  Learn from './Learn/Learn';

import {BrowserRouter as Router, Route, Link, Redirect} from 'react-router-dom';

class Course extends Component{
	constructor(props)
	{
		super(props);
		
		this.state={lesson :[],

			publiclesson:
			[
				{
				author: "vantuhym",
				cards:[
				{_id: "5cd9530e046226101b422c8e", front: "hello", back: "xin chao"},
				{_id: "5cd9530e046226101b422c8d", front: "apple", back: "qua tao"},
				{_id: "5cd9530e046226101b422c8c", front: "table", back: "bang"},
				{_id: "5cd9530e046226101b422c8b", front: "school", back: "truong hoc"}],

				description: "giup hoc tot tieng anh",
				title: "tieng anh",
				_id: "5cd9530e046226101b422c8"
				},

				{
				author: "vantuhym",
				cards:[
				{_id: "5cd9530e046226101b422c8e", front: "byoki", back: "benh"},
				{_id: "5cd9530e046226101b422c8d", front: "biyutsukan", back: "bao tang mi thuat"},
				{_id: "5cd9530e046226101b422c8c", front: "kyoka", back: "su cho phep"},
				{_id: "5cd9530e046226101b422c8b", front: "connichiwa", back: "xin chao buoi chieu"},
				{_id: "5cd9530e046226101b422c8ea", front: "byoki", back: "benh"},
				{_id: "5cd9530e046226101b422c8db", front: "biyutsukan", back: "bao tang mi thuat"},
				{_id: "5cd9530e046226101b422c8cc", front: "kyoka", back: "su cho phep"},
				{_id: "5cd9530e046226101b422c8bd", front: "connichiwa", back: "xin chao buoi chieu"}
				],

				description: "Minna no Nihongo và Genki là hai giáo trình rất quen thuộc đối với những ai mới làm quen với tiếng Nhật. Phần đông những trung tâm dạy tiếng Nhật ở Việt Nam chọn Minna no Nihongo làm giáo trình chính.",
				title: "Tiếng Nhật Bài 30 ",
				_id: "5cd9530e046226101b422c8a"
				
				}

			],
			currentLesson:{
				}
		}

		this.setStateCurrentLesson = this.setStateCurrentLesson.bind(this);
		
		this.getdatafromserver = this.getdatafromserver.bind(this);
		this.deleteCourse = this.deleteCourse.bind(this);

	}

	deleteCourse(_id)
	{
		return((event)=>{
			let i;
			
			axios({
				method:'post',
				url:'http://localhost:3030/deletelessonbyid',
				data:{idlesson:_id} 
			}).then(res=>{
    		console.log(res);
   
    		
    	})
    	.catch(error=>{
    		console.log(error)
    	});

			for(i=0; i<this.state.lesson.length; i++)
			{
				if(this.state.lesson[i]._id === _id)
				{
					this.setState({
						...this.state,
						lesson:[
						...this.state.lesson.slice(0,i),
						...this.state.lesson.slice(i+1)
						]
					});
					console.log(this.state);
				}
				
			}

		});
		

	}
	 setStateCurrentLesson(lesson)
	 {
	 	
	 	return (event)=>
	 	{
		 	//console.log("dmdmd");
		 	let newState = this.state;
		 	this.setState({
		 		...newState,currentLesson:lesson
		 	});
	 	}
	 }

	 getdatafromserver(){
	 	console.log("reset data");
	 	axios({
    		method:'get',
    		url:'http://localhost:3030/alllesson'
    	}).then(res=>{
    		console.log(this.state);
    		if(res.data.success === false)
    		{
    			this.setState(
	    		{
	    			...this.state,
	    			lesson:[],
	    			currentLesson:{}

	    		});
    		}
    		else
    		this.setState(
    		{
    			...this.state,
    			lesson:res.data.lessonresult,
    			currentLesson:res.data.lessonresult[0]

    		});
    		
    	})
    	.catch(error=>{
    		console.log(error)
    	});
	 }
	componentDidMount()
	{
		
		this.getdatafromserver();

	}
	

	/*componentDidUpdate()
	{
		console.log("didmount chuwa");
		axios({
    		method:'get',
    		url:'http://localhost:3030/alllesson'
    	}).then(res=>{
    		
    		this.setState(
    		{
    			lesson:res.data.lessonresult,
    			currentLesson:res.data.lessonresult[0]

    		});
    		
    	})
    	.catch(error=>{
    		console.log(error)
    	});

	}*/componentDidUpdate()
	{

	}
	

	render()
	{console.log("render course");
		
			let {lesson} = this.state;
			let {publiclesson} = this.state;

			let {currentLesson} = this.state;
			return(
				<div>
					<Route path="/course/KhoaHoc" render={(props)=>
						<KhoaHoc {...props}   
						key={lesson._id}  
						lesson={lesson} 
						resetdata={this.getdatafromserver}
						deleteCourse={this.deleteCourse}
						publiclesson={publiclesson}
						clicked={this.setStateCurrentLesson}
						logged={this.props.logged}
						/>
					}/>
        			 
        			{
        				
        				lesson.map((less)=>{
        					let mypath = "/course/"+ less._id;
        					return(
        						<div key={less._id}>
	        						<Route path={mypath}  exact render={(props) => 
	        							<Lessondetail {...props}  
	        							lesson={currentLesson} 
	        							key={currentLesson._id}
	        							edit={true}/>} />
	        						<Route path={mypath+"/edit"}  exact render={(props) => 
	        							<LessonEdit {...props}  
	        							lesson={currentLesson} 
	        							key={currentLesson._id}/>} />
	        						<Route path={mypath+"/learn"}  exact render={(props) => 
	        							<Learn {...props}  
	        							listcard={currentLesson.cards} 
	        							idlesson={currentLesson._id}/>}/>

        						</div>
        					)
        				})
        			}
        			{
        				publiclesson.map((less)=>{
        					let mypath = "/course/"+ less._id;
        					return(
        						<div key={less._id}>
	        						<Route path={mypath}  exact render={(props) => 
	        							<Lessondetail {...props}  
	        							lesson={currentLesson} 
	        							key={currentLesson._id} 
	        							edit={false}
	        							/>} />
	        						
	        						<Route path={mypath+"/learn"}  exact render={(props) => 
	        							<Learn {...props}  
	        							listcard={currentLesson.cards} 
	        							idlesson={currentLesson._id}/>}/>

        						</div>
        					)
        				})
        			}

				</div>

			)
	}
}

export default Course;


