import React,{Component} from 'react';
import "./LessonEdit.css";

import { Redirect } from 'react-router-dom';

import axios from 'axios';
class LessonEdit extends Component{
	constructor(props)
	{
		super(props);
		
		this.state={
			back:'',
			front:'',
			lesson:this.props.lesson,
			redirect: false
		};
		this.title = this.state.lesson.title;	
		this.description = this.state.lesson.description;	
		this.handleChangedescription = this.handleChangedescription.bind(this);

		this.handleChangeTitle = this.handleChangeTitle.bind(this);

		this.handleChangeBack = this.handleChangeBack.bind(this);
		this.handleChangeFront = this.handleChangeFront.bind(this);
		this.handleChangeListBack = this.handleChangeListBack.bind(this);
		this.handleChangeListFront = this.handleChangeListFront.bind(this);
		this.addToList = this.addToList.bind(this);
		this.removeCard= this.removeCard.bind(this);
		this.sentDataToServer = this.sentDataToServer.bind(this);
		this.setRedirect = this.setRedirect.bind(this);
	}

	setRedirect()
	{
		let currentState = this.state;
		this.setState({...currentState,redirect:true});
	}
	renderRedirect()
	{
		if(this.state.redirect === true)
		{
			return (<Redirect to='/course/khoahoc' />)
		}
	}

	handleChangedescription(event)
	{
		console.log(event.target.getAttribute("index"));
		this.setState(
			{
				...this.state,
				lesson:{
					...this.state.lesson,description:event.target.value
				}
			});
	}

	handleChangeTitle(event)
	{
		

		console.log(event.target.getAttribute("index"));
		this.setState(
			{
				...this.state,
				lesson:{
					...this.state.lesson,title:event.target.value
				}
			});
	}

	handleChangeListFront(card)
	{
		return((event)=>
		{
			let index = this.state.lesson.cards.indexOf(card);
			this.setState({
				...this.state,
				lesson:{...this.state.lesson,
						cards:[
							...this.state.lesson.cards.slice(0,index),
							{ ...card,front:event.target.value},
							...this.state.lesson.cards.slice(index+1)
						]}
			});
		})
	}

	sentDataToServer()
	{
		console.log({...this.state.lesson});
		axios({
			method:'post',
			url:'http://localhost:3030/updatelesson',
			data:{...this.state.lesson}
		}).then(res=>{
    		console.log(res);
    		if(res.data.success === true)
    		{
    			this.setRedirect();
    		}

    	})
    	.catch(error=>{
    		console.log(error)
    	});


	}

	removeCard(card)
	{
		return (event)=>
		{
			let index = this.state.lesson.cards.indexOf(card);
			this.setState({
				...this.state,
				lesson:{...this.state.lesson,
						cards:[
							...this.state.lesson.cards.slice(0,index),
							
							...this.state.lesson.cards.slice(index+1)
						]}
			});
		}
	}

	handleChangeListBack(card)
	{
		return((event)=>
		{

			let index = this.state.lesson.cards.indexOf(card);
			this.setState({
				...this.state,
				lesson:{...this.state.lesson,
						cards:[
							...this.state.lesson.cards.slice(0,index),
							{ ...card,back:event.target.value},
							...this.state.lesson.cards.slice(index+1)
						]}
			});
		});
	}

	handleChangeFront(event)
	{

		
		this.setState({
			...this.state,
			front:event.target.value
		});
	}

	handleChangeBack(event)
	{
		
		this.setState({
			...this.state,
			back:event.target.value
		});
	}
	addToList(event)
	{
		if(this.state.back !== '' && this.state.front !== '')
		this.setState({
			...this.state,
			lesson:{
				...this.state.lesson,
				cards:[
					...this.state.lesson.cards,
					{front:this.state.front,back:this.state.back}
				]
			},
			front:'',
			back:''

		});
	}

	render()
	{
		return(
			<div className="container-lesson-edit">
				<div className="enter-lesson-edit">


						{
							this.renderRedirect()
						}
								
							<div className="title-and-des">
							<input type="text" placeholder=" Tiêu Đề Bộ thẻ"  onChange={this.handleChangeTitle} value={this.state.lesson.title}/>
							<br/>
							
					
							<input type="text"  placeholder=" Tiêu Đề Bộ thẻ"  onChange={this.handleChangedescription} value={this.state.lesson.description}/>

							</div>
							<button className="btn" onClick={this.sentDataToServer}> Cập Nhật</button>
					
					
				</div>
				<div className="view-card-lesson-edit">
					<table border="no-border">
						 <tr> 
						    <th>Front</th>
						    <th>Back</th>
						    <th class="no-border"></th>
						 </tr>
					{
						this.state.lesson.cards.map((card,id)=>{
							
							return(
								<tr>
									<td>
										<input type="text" 
										value={card.front} 
										onChange={this.handleChangeListFront(card)} />
									</td>
									<td>
										<input type="text"  
										value={card.back} 
										onChange={this.handleChangeListBack(card)}/>
									</td>
									<td class="no-border">
									<button  className="btn" onClick={this.removeCard(card)}>xoas</button>
									</td>
								</tr>
							)
						})
					}
					<tr>
						<td>
							<input type="text" value={this.state.front} onChange={this.handleChangeFront} />
						</td>
						<td>
							<input type="text"  value={this.state.back} onChange={this.handleChangeBack} />
						</td>
						<td>
							<button className="btn" onClick={this.addToList}>theem</button>
						</td>
					</tr>
					
					
					
				</table>
					</div>
			</div>
		);
	}
}
export default LessonEdit;


