import React, {Component} from 'react';
import axios from 'axios';
import { Redirect } from 'react-router-dom';

import './DangNhap.css';
class DangNhap extends Component {
	constructor(props)
	{	
		super(props);
		this.state = {
			username:'',
			password:'',
			message:'',
			redirect:false
		};

		this.onHandleChangeUsername = this.onHandleChangeUsername.bind(this); 
		this.onHandleChangePassword = this.onHandleChangePassword.bind(this); 
		this.submitData = this.submitData.bind(this);
		this.setRedirect = this.setRedirect.bind(this);
		

	}
	renderError()
	{
		if(this.state.message ==='')
			return;
		
		else return(
			<div>

		                <hr/>
		               	<p className="error">{this.state.message}</p>
		    </div>

		);
	}
	setRedirect()
	{
		let currentState = this.state;
		this.setState({...currentState,redirect:true});
	}
	renderRedirect()
	{
		if(this.state.redirect === true)
		{
			return (<Redirect to='/course/khoahoc' />)
		}
	}

	componentDidMount(){
    //console.log(loginContext);
  	}
    submitData(){
    	
    	axios({
    		method:'post',
    		url:'http://localhost:3030/login',
    		data:{
    			username: this.state.username,
    			password:this.state.password
    		}
    	}).then(res=>{
    		console.log(res);
    		if(res.data.success === true)
	    		{
	    			this.props.fnlogged(true);
	    			this.setRedirect();
	    			//window.location.href="/course/khoahoc";
	    		}else
	    		this.setState({...this.state,message:"Tài khoản hoặc mật khẩu k chính xác xin vui lòng thử lại !!!"})
    	})
    	.catch(error=>{
    		console.log(error)
    	});

    	
       

        //window.location.href = "/khoahoc";
        
       
	}

	cancelbtn()
	{
		
		window.location.href = "/";
		/*axios.get('http://localhost:3030/alllesson').then(res=>{
    		console.log(res);
    	})
    	.catch(error=>{
    		console.log(error)
    	});
    	console.log("function tesst");*/
	}


	onHandleChangeUsername(event)
	{
		let currentState =  this.state;
		this.setState({...currentState,"username":event.target.value});
		//console.log(this.state);
	}

	onHandleChangePassword(event)
	{
		let currentState =  this.state;
		this.setState({...currentState,"password":event.target.value});
		//console.log(this.state);
	}

	render(){
		return (
				<div className="dangnhap">
			        	{
			        		this.renderRedirect()
			        	}

			            <div className="container">
			            	{
			        			this.renderError()
			        		}
			                <label htmlFor="uname"><b>Email</b></label>
			                <input type="text" placeholder="" name="uname" onChange={this.onHandleChangeUsername} id="user" required/>
			                <label htmlFor="psw"><b>Mật khẩu</b></label>
			                <input type="password" placeholder="" name="psw" onChange={this.onHandleChangePassword} required/>
			               	
			                		<button type="submit" onClick={this.submitData} >Đăng nhập</button>
			                		

			                
			                <label>
			                    <input type="checkbox" defaultChecked name="remember" /> Tự động đăng nhập
			                </label>
			            </div>
			            <div className="container">
			                <button type="button" className="cancelbtn" onClick={this.setRedirect}>Trở lại</button>
			                <i>
			                <span className="psw">Quên mật khẩu  <a href="#">mật khẩu?</a></span>
			                </i>
			                <i className="note">Chưa có tài khoản <a href="#" >Đăng ký</a>.</i>
			            </div>
			        
			    </div>

			)
	}

    
}

export default DangNhap;



