import React , {Component} from 'react'; 
import MenuBar from './components/MenuBar/menuBar';
import DangKy from './components/DangKy/dangKy';
import DangNhap from './components/DangNhap/dangNhap';
import KhoaHoc from './components/KhoaHoc/KhoaHoc';
import Course from './components/KhoaHoc/Course';
import {LoginProvider} from './context/login.context';
import Footer from './components/Footer/Footer';
import AddCourse from './components/KhoaHoc/AddCourse';
import { Redirect } from 'react-router-dom';



import {BrowserRouter as Router, Route, Link} from 'react-router-dom';

class App extends Component{
  constructor(props)
  {
    super(props);
    this.state={logged:false};
    this.fnlogged = this.fnlogged.bind(this);
  } 
  fnlogged(bien)
  {
    
    this.setState({logged:bien});
    console.log(this.state);
  }

  render()
  {
    return (
     
      <Router>
        <MenuBar logged={this.state.logged} fnlogged={this.fnlogged}/>
        
        <Route path="/course/add" component={AddCourse}/>
        <Route path="/course"    render={(props)=>
          <Course {...props} logged={this.state.logged}/>
        }/>

          <Route path="/congdong" component={()=>
          {
            return(<div>Cộng Đồng</div>);
          }}/>

          <Route path="/logout" component={()=>
          {

            return(<div>Thông tin</div>);
          }}/>
          <Route path="/user" component={()=>
          {
            return(<div>Thông tin tai khoan</div>);
          }}/>

          <Route path="/dangky" render={(props)=>
              <DangKy {...props}
              fnlogged={this.fnlogged}
              />
          }/>

          <Route path="/dangnhap" render={(props)=>
            <DangNhap {...props} 
            fnlogged={this.fnlogged}
            />
          }/>

          <Footer/>
          
        
      </Router>

      
      )
  }
}

export default App;



