import React,{Component} from 'react';
import './MenuBar.css';
import {Link} from 'react-router-dom';
import axios from 'axios';

class MenuBar extends Component {
	 constructor(props)
	 {
	 	super(props);
	 	this.onLogout = this.onLogout.bind(this);
	 }

	
	 onLogout()
	 {
	 	this.props.fnlogged(false);
	 	     axios({
              method:'get',
              url:'http://localhost:3030/logout'
            }).then(res=>{
              console.log(res);
              window.location.href = "/";
              
            })
            .catch(error=>{
              console.log(error)
            });


	 }

viewlogged()
{
	if(this.props.logged === false )
		return(
			<div className="content-right">	
				<Link to="/dangnhap">Đăng Nhập</Link>
				<Link to="/dangky">Đăng ký</Link>
				</div>);
	else
	{
		return(
			<div className="content-right">	
				<div onClick={this.onLogout}>Đăng Xuất</div>
			</div>);
	}
}


	render()
	{
		
		return (
		<div className="main-header">
			<div className="nav-bar">

				<div className="box-left" >

					<div className="box-logo"> 
						<Link to="/course/KhoaHoc">
						<img src="https://minder.vn/img/icons/icon-minder.png" alt=""/>
						</Link>
					</div>

				
					<ul className="list-menu" id="showMenu">
					
						<Link to="/course/KhoaHoc"  ><li>
							Khóa học
						</li></Link>

						<Link to="/congdong"><li>
							Cộng Đồng
						</li></Link>
						
						
						
						
						
					</ul>

					<div className="box-search">
								
					</div>

				</div>
					
				
				{
					this.viewlogged()
				}
			
						
				
				
			</div>
		</div>
		);
	}
}

export default MenuBar;


