import React,{Component} from 'react';
import './Card.css';
import {Link} from 'react-router-dom';


class Card extends Component{
	 constructor(props)
	 {
	 	super(props)

	 }
	
	render()
	{
		return( 
			<div className="card">
				
				<Link to={this.props.path}>
					<img src="http://data.minder.vn/courses/104000002.jpg" alt="" width="200px" height="200px"/>
					<h3>{this.props.title}</h3>
												
					<div className="lengthword"> <span><img src="https://minder.vn/img/icons/ic_word.png" alt=""/></span>   {this.props.lengthWord} Thẻ</div>
					<div className="view"><span><img src="https://minder.vn/img/icons/ic_time.png"/></span> 100</div>
				</Link>
				
			</div>

			

			)
	}
}

export default Card;


