import React,{Component} from 'react';
import './KhoaHoc.css';
import Card from './Card';
import axios from 'axios';
import {BrowserRouter as Router, Route, Link} from 'react-router-dom';

class KhoaHoc extends Component{
	constructor(props)
	{
		super(props);
		//console.log(this.props);
		this.state = this.props;
		this.renderloginfalse = this.renderloginfalse.bind(this);


	}
	componentDidMount()
	{
		this.props.resetdata();
	}
	renderloginfalse()
	{
		console.log(this.props);
		if(this.props.logged === true)
			return(
				<div className="content-card"> 
				<Link to="/course/add">
				<div className="card" >
					
					<img src="https://img.icons8.com/android/96/000000/plus.png" alt="" width="200px" height="200px"/>
				</div>
				</Link>
				</div>

			);
		else
			return (<div className="message">Bạn chưa đăng nhập hệ thống </div>);
	}
	
	render()
	{
		
		console.log("render Khoa hocj");
		let {lesson, clicked,publiclesson} = this.props;
		
		return(

			<div className="content-khoa-hoc">
			<div className="title-mykhoahoc">
					Khóa Học Của Tôi
			</div>
			<div className="list-card">
				
			{
			 	lesson.map((less)=>{
			 		let mypath = "/course/"+ less._id;
			 		
				 	return(
				 	<div className="content-card">	
					 	<div onClick={clicked(less)} key={less._id}  > 
					 		<Card  
					 		
					 		idlesson={less._id} 
					 		title={less.title} 
					 		lengthWord={less.cards.length}
					 		deleteCourse={this.props.deleteCourse}
					 		path={mypath}/>
					 	</div>	
					 	<span className="deleteCourse" 
						onClick={this.props.deleteCourse(less._id)}>
						delete
						</span>
				</div>
				 	)
				})
			 }
			

			 
			
				{
					this.renderloginfalse()
				}
			
			 </div>
			<br/>



			<div className="title-mykhoahoc">
					Gợi Ý Khóa Học 
			</div>
			<div className="list-card">
				
			{
			 	publiclesson.map((less)=>{
			 		let mypath = "/course/"+ less._id;
			 		
				 	return(
				 	<div className="content-card">	
					 	<div onClick={clicked(less)} key={less._id}  > 
					 		<Card  
					 		
					 		idlesson={less._id} 
					 		title={less.title} 
					 		lengthWord={less.cards.length}
					 		deleteCourse={this.props.deleteCourse}
					 		path={mypath}/>
					 	</div>	
					 	
				</div>
				 	)
				})
			 }
			 </div>

			 
			
			</div>

			)
	}
}

export default KhoaHoc;


