import React,{Component} from 'react';
import './ShowCard.css';
import {Link} from 'react-router-dom';


class ShowCard extends Component{
	 constructor(props)
	 {
	 	super(props)
	 	this.state= {clicked :false};
	 	this.handleClick = this.handleClick.bind(this);

	 }
	 handleClick()
	 {
	 		
	 	return (event)=>{
	 		//console.log(event.target);
	 		//console.log(id);
	 		this.setState({clicked:!this.state.clicked});
	 	}
	 	
	 }
	render()
	{
		let {clicked} = this.state;
						let classBack;
						let classFront; 
						if(clicked === true)
						{
							classBack = "back showBack";
							classFront = "front hideFront";
						}
						else
						{
							classBack = "back hideBack";
							classFront = "front showFront";
						}
		return(
			<div  className="word-card-learn" onClick={this.handleClick()}>
				<div  className={classFront} >
					<div className="concon">
						<h3>{this.props.card.front}</h3>
					</div>
				</div>

	    		<div className={classBack}>
					<div className="concon">
						<h3>{this.props.card.back}</h3>
					</div>
				</div>

			</div>

			)
	}
}

export default ShowCard;


