import React,{Component} from 'react';
import './Learn.css';

import CustomComponent from './CustomComponent';
import {BrowserRouter as Router, Route, Link} from 'react-router-dom';


class Learn extends Component{

	constructor(props)
	{
		super(props);
		this.state ={
			index:0,
			indexLearn:0,
			dataLearnCard:[],
			currentTag:'ShowCard',
			status:0
		}
		this.listcard = [].concat(this.props.listcard);

		this.changeCurrentCard = this.changeCurrentCard.bind(this);
		this.listcardlearn =[{...this.props.listcard[0],loop:0}];


	}
	/*componentDidMount()
	{
		changeCurrentCard(0);
	}*/

	renderbtn(){
		if(this.state.status === 1)
		{
			
			let path = "/course/"+this.props.idlesson;
			
			return(
				<Link to={path} ><button className="btn-learn">  Đã Học Xong</button></Link>
			);
		}
		else
		{
			return(
				<button onClick={this.changeCurrentCard(this.state.index)} className="btn-learn"> Tiếp Tục</button>
			);
		}
	}

	changeCurrentCard(index)
	{
		return()=>{
			//console.log(this.props.listcard);
		
			
				
					
			
				
			if(index < this.props.listcard.length-1)
			{
				
					let xx; // ti le ram dom vao learn
					if(index < this.props.listcard.length/2)
						xx=2; else xx= 2;
					if(index < 2) xx=1;
					let ran = Math.floor(Math.random() * xx);  
					if(ran === 0)
					{
						
							this.listcardlearn.push({...this.props.listcard[this.state.index+1],loop:0});
							
							
								this.setState({
									...this.state,
									currentCard:this.props.listcard[index+1],
									index:index+1,currentTag:'ShowCard'
								});
							
						

					}
					else
					{
						//learn card
						
						let countLoop = 0;
						this.listcardlearn.map((card)=>{countLoop+= card.loop});
						console.log(countLoop);
						if(countLoop < this.listcardlearn.length)
						{
							let randomIndex = Math.floor(Math.random() * this.listcardlearn.length);
							while(this.listcardlearn[randomIndex].loop >0)
								randomIndex = Math.floor(Math.random() * this.listcardlearn.length);

							this.listcardlearn[randomIndex].loop++;

							this.listcard.splice(randomIndex,1);
							this.listcard =[].concat(
								this.listcard[randomIndex%this.listcard.length],
								this.listcard[(randomIndex+1)%this.listcard.length],
								this.listcard[(randomIndex+2)%this.listcard.length]
							);
							let random = Math.floor(Math.random() * 4);  
							this.listcard =[].concat([
								...this.listcard.slice(0,random),
								this.props.listcard[randomIndex],
								...this.listcard.slice(random)]);


							this.setState(
								{...this.state,
								currentTag:'LearnCard',
								dataLearnCard:this.listcard,
								indexLearn:randomIndex}
							);
						}
						else
							this.setState({...this.state,status:1})
						
					}
			}
			else {
				//learn card
				
						
						let countLoop = 0;
						this.listcardlearn.map((card)=>{countLoop+= card.loop});
						console.log(countLoop);
						if(countLoop < this.listcardlearn.length)
						{
							let randomIndex = Math.floor(Math.random() * this.listcardlearn.length);
							while(this.listcardlearn[randomIndex].loop >0)
								randomIndex = Math.floor(Math.random() * this.listcardlearn.length);

							this.listcardlearn[randomIndex].loop++;

							this.listcard.splice(randomIndex,1);
							this.listcard =[].concat(
								this.listcard[randomIndex%this.listcard.length],
								this.listcard[(randomIndex+1)%this.listcard.length],
								this.listcard[(randomIndex+2)%this.listcard.length]
							);
							let random = Math.floor(Math.random() * 4);  
							this.listcard =[].concat([
								...this.listcard.slice(0,random),
								this.props.listcard[randomIndex],
								...this.listcard.slice(random)]);


							this.setState(
								{...this.state,
								currentTag:'LearnCard',
								dataLearnCard:this.listcard,
								indexLearn:randomIndex}
							);
						}
						else
							this.setState({...this.state,status:1})
			}

			
		}

	}

	render()

	{	
		return (
			<div className="banner-learn">
				<div className="wrap-learn">
					<CustomComponent 
					currentCard={this.props.listcard[this.state.index]}
					tag={this.state.currentTag}
					dataLearnCard={this.state.dataLearnCard}
					currentCardLearn={this.props.listcard[this.state.indexLearn]}
					/>
					{
						this.renderbtn()
					}
					</div>
			</div>
			
		)
	}

}
export default Learn;


