import React, {Component} from 'react';
import axios from 'axios';
import './DangKy.css';
class DangKy extends Component {
	constructor(props)
	{
		super(props);
		this.state ={
			account:{
				username:'',
				email:'',
				password:''
			},
			message:'',
			password1:'',
			password2:''
		};

		this.changeUsername = this.changeUsername.bind(this);
		
		this.changeEmail = this.changeEmail.bind(this);
		this.changePassword = this.changePassword.bind(this);
		this.onClickDangky = this.onClickDangky.bind(this);
	}

	changeUsername(event)
	{
		this.setState({
			...this.state,
			
				account:{...this.state.account, username:event.target.value}
			
		});
		console.log(event.target.value);

	}

	changeEmail(event)
	{
		this.setState({
			...this.state,
			
				account:{...this.state.account, email:event.target.value}
			
		});
		console.log(event.target.value);
	}

	changePassword(type)
	{
		if(type === 1) 
		return(
			(event)=>
				{
					this.setState({
						...this.state,
						password1:event.target.value
					});

				}
		)
		else
		return(
			(event)=>
				{
					this.setState({
						...this.state,
						password2:event.target.value
					});

				}
		)
	}

	renderError()
	{
		if(this.state.message ==='')
			return;
		
		else return(
			<div>

		                <hr/>
		               	<p className="error">{this.state.message}</p>
		    </div>

		);
	}
	onClickDangky(event)
	{
		if(this.state.account.username ==='') 
			{
				alert("Trường username  Không Được Bỏ Trống !!!!");
				return;
			}
		if(this.state.account.email ==='')
		{
			alert(" Trường Email Không Được Bỏ Trống !!!!");
			return;
		}

		


			this.setState({
				...this.state,
				account:{...this.state.account,password:this.state.password1}
			});


		if(this.state.password1 === this.state.password2&& this.state.password1 !=='')
		{
			//gui request
			console.log(this.state);
			axios({
				method:'post',
				url:'http://localhost:3030/createAcount',
				data:this.state.account

			}).then(res=>{
	    		console.log(res);
	    		if(res.data.status === true)
	    		{
	    			this.props.fnlogged(true);
	    			//window.location.href="/course/khoahoc";
	    		}else
	    		this.setState({...this.state,message:"username của bạn có người đã sử dụng !!! vui lòng nhập lại "});
	    	})
	    	.catch(error=>{
	    		console.log(error)
	    	});
		}
		else
		{
			alert("Thông Tin Đăng Kí Không Hợp Lệ  !!!! \n có thể có password không khớp");
			return;
		}
	}

	render(){
		return(
			<div className="dangky">
		        
		            <div className="container">

		                <h1>Đăng ký</h1>
		                <p>Vui lòng cung cấp thông tin để đăng ký.</p>
		                {this.renderError()}
		                <hr/>
		                <label htmlFor="UserName"><b>UserName</b></label>

		                <input type="text" 
		                placeholder="Vd:vantuhym"  
		                value={this.state.account.username}
		                onChange={this.changeUsername}
		                name="username" 

		                required/>
		                <label htmlFor="email"><b>Email</b></label>

		                <input type="text" 
		                placeholder="Vd: ckpvthuongft@gmail.com"
		                name="email"
		                value={this.state.account.email}
		                onChange={this.changeEmail}
		                required/>

		                <label htmlFor="psw"><b>Mật khẩu( ít nhất 8 ký tự bao gồm cả chữ và số)</b></label>

		                <input type="password" 
		                placeholder="" 
		                name="psw" 
		                value={this.state.password1}
		                onChange={this.changePassword(1)}
		                required/>

		                <label htmlFor="psw-repeat"><b>Nhập lại mật khẩu</b></label>

		                <input type="password" 
		                placeholder="" 
		                name="psw-repeat"
		                value={this.state.password2}
		                onChange={this.changePassword(2)} 
		                required/>

		               

		                <p>Bấm vào đăng ký sẽ đồng nghĩa với chấp thuận các điều khảon của chúng tôi  <a href="#" >Chính sách và bảo mật</a>.</p>
		                <div className="clearfix">
		                    <button type="button" className="cancelbtn">Trở lại</button>
		                    <button 
		                    	type="button" 
		                    	className="signupbtn"
		                    	onClick={this.onClickDangky}>
		                    	Đăng Ký 
		                    </button>
		                </div>
		                <i>Đã có tài khoản <a href="#">Đăng nhập</a>.</i>
		            </div>
		        
		    </div>


			)
	}
}

export default DangKy;


