import React , {Component} from 'react';

export  const loginContext = React.createContext();

export  class LoginProvider extends Component{
	constructor(props)
	{
		super(props);
		this.state= {loged:false};
		this.changeLoged = this.changeLoged.bind(this);
	}

	changeLoged(data)
	{
		console.log("test");
		this.setState({loged:data});
	}

	render()
	{
			return(
				<loginContext.Provider value={{
					loged:this.state.loged,
					changeLoged:this.changeLoged
					}}>

					{this.props.children}
				</loginContext.Provider>
			)
	}
}


